.hat {
  height: 100px;
  padding: 0;

  .flex {
    justify-content: space-between;
    align-items: center;
  }
}

.logo {
  font-weight: 700;
  color: $accent;
  font-size: 12px;
  text-transform: uppercase;
  transition: .2s ease;
  position: relative;

  &:after {
    content: "";
    height: 2px;
    width: 0;
    background: $accent;
    position: absolute;
    bottom: 0;
    transition: .2s ease;
  }

  &:hover {
    color: $accent;

    &:after {
      width: 100%;
    }
  }

  &-wrap {
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.main__menu {
  &-wrap {
    width: 82%;
  }

  &-list {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100px;
    font-size: 12px;
  }

  &-link {
    color: #000000;
    transition: .2s ease;

    &:hover {
      color: $accent;
    }
  }
}

.main__info {
  height: 100px;
  font-size: 12px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &-text {
    font-weight: 700;
    white-space: nowrap;
    margin-right: 7px;

    &:before {
      font-family: "Font Awesome 5 Free";
      font-weight: 900;
      color: $accent;
      margin-right: 5px;
    }

    &-address:before {
      content: "\f3c5";
    }

    &-tel:before {
      content: "\f095";
    }
  }
}

.social-link {
  display: flex;
  width: 22px;
  height: 22px;
  justify-content: center;
  align-items: center;
  background: $accent;
  border-radius: 50%;
  color: #ffffff;
  transition: .2s ease;

  &:hover {
    transform: scale(1.6);
    color: #ffffff;
  }
}

.accent-btn {
  padding: 11px 21px;
  border-radius: 21px;
  background: $accent;
  color: #ffffff;
  transition: .2s ease;
  text-transform: none;
  font-weight: 600;
  border: none;
  cursor: pointer;
  outline: none !important;

  &:hover {
    transform: scale(1.1);
    color: #ffffff;
  }

  i {
    font-size: 13px;
  }
}

.header {
  &__carousel {
    overflow: visible;
    height: 100%;
  }

  &-inner {
    display: flex;
    justify-content: center;
    flex-direction: column;
    height: 63vh;
    z-index: 1;
    position: relative;
  }

  &__images {
    transition: .5s ease;

    img {
      position: absolute;
      z-index: 0;
    }

    &-bouquet {
      right: 31px;
      top: 40px;

      &-shadow {
        right: 136px;
        top: 544px;
      }
    }

    &-blur-flower {
      right: 390px;
      top: 25px;
    }

    &-left-leaf {
      right: 509px;
      top: 124px;
    }

    &-left-fl {
      right: 531px;
      top: 319px;
    }

    &-right-fl {
      right: -94px;
      top: 39px;
      transform: scale(-1, 1);
      width: 78px;
    }

    &-blur-leaf {
      right: -111px;
      top: 267px;
    }

    &-right-leaf {
      right: -23px;
      top: 458px;
      transform: scale(-1, -1);
      width: 42px;
    }
  }
}

.more-btn {
  padding-left: 42px;
  padding-right: 42px;

  &-wrap {
    margin-top: 73px;
  }
}

.first-title-after {
  font-size: 24px;
}

.bg-all {
  background: url("../img/content-bg-wave.jpeg") no-repeat center top / cover;
}

.about {
  padding-top: 130px;

  &__text:not(:last-child) {
    margin-top: 74px;
    margin-bottom: 28px;
  }

  &-img-wrap {
    border-radius: 10px;
    overflow: hidden;
  }

  &-balloons-img-wrap {
    margin-top: 74px;
  }

  &__counter {
    font-size: 332px;
    position: absolute;
    font-weight: 600;
    background: url("../img/250-bg.jpeg") no-repeat;
    background-position-x: -182px;
    background-position-y: 132px;
    background-size: 940px 709px;
    color: transparent;
    -webkit-background-clip: text;
    right: 36px;
    bottom: -67px;

    &-img {
      position: absolute;
      right: 161px;
      bottom: 30px;
    }


    &-wrap {
      position: relative;
      height: 100%;
      width: 100%;
    }

    &-clients {
      background: #ffffff;
      border-radius: 10px;
      padding: 35px 40px;
      width: 300px;
      position: absolute;
      bottom: 68px;
      right: 23px;
    }
  }
}

.accent-text {
  color: $accent;
  font-weight: 600;
}

.advantages {
  background: rgba(255, 32, 79, .07);

  .second-title {
    margin-bottom: 60px;
  }

  &__list {
    display: grid;
    grid-gap: 30px;
    grid-template-columns: repeat(4, 1fr);

    &-item {
      text-align: center;
      padding: 96px 20px 0;
      transition: .2s ease;

      &:hover {
        transform: scale(1.1);
      }

      @each $i in fireworks, balloons, bouquet {
        &-#{$i} {
          background: url("../img/#{$i}-icon.svg") no-repeat center top / 60px 60px;
        }
      }

      &-present {
        background: url("../img/present-icon.png") no-repeat center top;
      }
    }
  }
}


.goods {
  &__item {
    padding: 31px 43px 45px;
    background: #ffffff;
    border-radius: 5px;
    text-align: center;
    transition: .2s ease;
    position: relative;

    &:hover {
      transform: scale(1.1);
    }

    &-remove {
      position: absolute;
      top: 26px;
      right: 26px;
      cursor: pointer;
      transition: .2s ease;
      font-size: 20px;
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;

      i {
        transition: .2s ease;
      }

      :hover {
        color: $accent;
        transform: scale(1.6);
      }
    }

    &-title {
      font-size: 17px;
      margin-bottom: 3px;
    }

    &-preview {
      margin: 0 auto;
    }

    &-cost {
      font-size: 30px;
      font-weight: 700;
    }

    &-btn {
      text-transform: uppercase;
      border-radius: 25px;
      border: 2px solid $accent;
      position: relative;
      font-weight: 600;
      padding: 5px 28px;
      margin-top: 10px;
      display: inline-block;
      color: $accent;
      transition: .2s ease;
      background: none;
      cursor: pointer;
      outline: none !important;

      &:hover {
        color: #ffffff;
        background: $accent;
      }

      &:after {
        content: "";
        display: block;
        width: 16px;
        height: 16px;
        position: absolute;
        background: #ff204f;
        border: 4px solid #fff;
        border-radius: 50%;
        right: -4px;
        top: -2px;
      }
    }

    &-wrap {
      display: grid;
      padding-top: 57px;
      grid-column-gap: 30px;
      grid-row-gap: 48px;
      grid-template-columns: repeat(4, 1fr);
      animation: fadeEffect 1s ease;
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    width: 51%;

    &-tab {
      font-weight: 700;
      color: #c0c0c0;
      background: none;
      border: none;
      cursor: pointer;
      outline: none !important;
      transition: .2s ease;

      &-active,
      &:hover {
        color: $accent;
      }
    }
  }
}

@keyframes fadeEffect {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.more-goods {
  font-size: 12px;
  color: #000000;
  text-decoration: underline;
  transition: .2s ease;

  &:hover {
    color: $accent;
    text-decoration: underline;
  }

  &-wrap {
    text-align: center;
    margin-top: 45px;
  }
}

.feedback {
  background: url("../img/feedback-bg.png") no-repeat center top / cover;

  &__carousel {

    &-item {
      text-align: center;
      padding: 48px 20px;
      background: #ffffff;
      border-radius: 8px;
    }
  }

  &__img-wrap {
    height: 51px;
    width: 51px;
    border-radius: 50%;
    margin: 0 auto;
    overflow: hidden;
    margin-bottom: 22px;
  }

  &__name {
    font-weight: 600;
    margin-bottom: 5px;
  }

  &__text {
    line-height: 1.75em;
    font-weight: 300;
  }
}

.owl-dot {
  outline: none !important;
  background: none;
  border: none;
}

.owl-dots {
  text-align: center;
  margin-top: 52px;
}

.owl-dot span {
  background: transparent;
  border-radius: 50%;
  display: block;
  height: 10px;
  width: 10px;
  margin-right: 4px;
  border: 1px solid #000;
  cursor: pointer;
}

.active span {
  background: #000000;
}

.owl-dot:last-child span {
  margin-right: 0;
}

.bg__bear {
  position: absolute;
  pointer-events: none;

  &_red {
    left: 0;
    top: -224px;
  }

  &_violet {
    right: 139px;
    top: 296px;
  }
}

.footer {
  display: flex;
  align-items: center;
}

.overlay {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(#000, 0.6);
  cursor: pointer;
  z-index: 5;
}

.modal-form,
.modal-form-order{
  display: none;
  background: lighten($accent, 50%);
  position: fixed;
  top: 50%;
  left: 50%;
  width: 400px;
  height: 422px;
  margin-left: -200px;
  margin-top: -211px;
  z-index: 6;
  animation: animateTop .4s ease;
  box-shadow: 0 0 20px 0 inset rgba(#000, .3);
  padding: 20px;
  border-radius: 20px;
}

@keyframes animateTop {
  from {
    top: -50%;
    opacity: 0
  }
  to {
    top: 50%;
    opacity: 1
  }
}

.close-modal,
.close-modal-Order{
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 20px;
  font-size: 20px;
  color: #000;
  transition: .2s ease;
  width: 20px;
  height: 20px;
  z-index: 8;
  background: none;
  border: none;

  &:hover {
    transform: scale(1.6);
    color: $accent;
  }
}

.popup__title {
  text-align: center;
  text-transform: uppercase;
  font-size: 18px;
}

.feedback-form {
  padding: 20px;
  position: relative;
}

.form__input {
  width: 100%;
  padding: 20px 30px;
  margin: 15px 0;
  border: 2px solid $accent;
  outline: none !important;
  border-radius: 20px;
  transition: .2s ease;

  &:focus {
    transform: scale(1.05);
    box-shadow: 0 0 35px 0 rgba(#0D0D0D, .6);

    &::placeholder {
      font-size: 12px;
    }
  }
}

::placeholder {
  font-size: 18px;
  text-transform: uppercase;
  color: darken($accent, 20%);
  font-family: Arial, sans-serif;
  transition: .2s ease;

  &:focus {
    font-size: 15px;
  }
}

.form__sbm,
.form__sbm-order{
  background: $accent;
  border-radius: 40px;
  color: #ffffff;
  text-transform: uppercase;
  cursor: pointer;
  transition: .2s ease;
  font-weight: 600;
  margin-top: 50px;

  &:hover {
    transform: scale(1.1);
  }
}

.header {
  .owl-item {
    .header__images {
      opacity: 0;
    }
  }

  .active {
    .header__images {
      opacity: 1;
    }
  }
}

.owl-carousel .owl-item img {
  width: auto;
  z-index: 0;
}

.header {
  .owl-carousel,
  .owl-stage-outer {
    overflow: visible !important;
    z-index: 0;
  }
}

.owl-custom-dots {
  position: relative;
  z-index: 2;
}


.basket {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 5;

  &__counter {
    position: absolute;
    top: 0;
    right: -6px;
    color: #fff;
    font-weight: 600;
    width: 15px;
    height: 15px;
    background: $accent;
    font-size: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
  }

  &__sbm {
    display: none;
    align-self: center;
    margin-top: 20px;
  }

  &__overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 4;
    cursor: pointer;
    background: rgba(#000, .5);
  }

  &__open {
    font-size: 20px;
    position: fixed;
    top: 30px;
    right: 30px;
    transition: .2s ease;
    cursor: pointer;
    z-index: 5;

    &:hover {
      transform: scale(1.4);
      color: $accent;
    }
  }

  &__body {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 992px;
    margin-left: -496px;
    min-height: 200px;
    max-height: 600px;
    margin-top: -300px;
    background-color: #fff;
    border-radius: 10px;
    padding: 25px;
    overflow: auto;
    animation: animateTop .5s ease;
    z-index: 6;
    display: flex;
    flex-direction: column;
    overflow-x: hidden;

    hr {
      background: $accent;
      height: 1px;
      width: 100%;
      margin: 0 auto;
      border-radius: 5px;
      margin-top: 25px;
    }
  }

  &__wrap {
    display: block;
    grid-gap: 0;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 25px;
    overflow-x: hidden;
    max-height: 378px;

    .goods__item {
      padding: 25px;
    }
  }

  &__empty {
    font-size: 24px;
    color: rgba(0, 0, 0, .4)
  }

  &__close {
    position: absolute;
    top: 30px;
    right: 30px;
    cursor: pointer;
    transition: .2s ease;
    z-index: 7;
    font-size: 20px;
    width: 20px;
    height: 20px;
    color: #000000;

    &:hover {
      color: $accent;
      transform: scale(1.6);
    }
  }

  .basket__total-cost {
    font-size: 18px;
  }
}

.basket-pulse {
  animation: pulse .5s ease;
}

@keyframes pulse {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(3);
  }
  to {
    transform: scale(1);
  }
}