@media screen and (max-width: 1199px) {
  .main__info {
    font-size: 10px;
  }

  .about__counter {
    font-size: 306px;
    background-position-y: 119px;
  }

  .about__text:not(:last-child) {
    margin-top: 70px;
    margin-bottom: 10px;
  }

  .goods__item {
    padding: 25px 20px 40px;
  }

  .goods__list {
    width: 58%;
  }
}

@media screen and (max-width: 991px) {
  .goods__list {
    width: 79%;
  }

  .header__images {
    img {
      position: absolute;
      max-width: 370px;
    }

    &-bouquet {
      right: -27px;
      top: 70px;

      &-shadow {
        right: 35px;
        top: 449px;
      }
    }

    &-blur-flower {
      right: 240px;
    }

    &-left-leaf {
      right: 367px;
    }

    &-left-fl {
      right: 300px;
    }

    &-blur-leaf {
      right: -128px;
    }

    &-right-leaf {
      right: -23px;
      top: 458px;
      transform: scale(-1, -1);
      width: 42px;
    }
  }

  .about__counter {
    font-size: 240px;
    background-position-y: 61px;
    right: -20;
    bottom: -60px;
  }

  .about__counter-img {
    right: 97px;
    bottom: 36px;
    height: 200px;
  }

  .about__counter-clients {
    padding: 20px 42px;
    bottom: 0;
    right: -16px;
  }

  .about__text {
    text-align: justify;
  }

  .about-balloons-img-wrap img {
    margin: 0 auto;
  }

  .goods__item-wrap,
  .basket__wrap{
    grid-template-columns: repeat(2, 1fr);
  }
  .basket__body {
    width: 690px;
    margin-left: -345px;
  }
}

@media screen and (max-width: 767px) {
  .advantages__list {
    grid-template-columns: repeat(2, 1fr);
  }

  .goods__list {
    width: 100%;
  }

  .first-title {
    font-size: 39px;
  }

  .main__info {
    font-size: 8px;
  }

  .social-link {
    font-size: 12px;
    width: 20px;
    height: 20px;
  }

  .header__images {
    &-bouquet {
      right: -49px;
      top: 131px;
      display: block !important;

      &-shadow {
        display: none;
      }
    }

    &-blur-flower {
      display: none;
    }

    &-left-leaf {
      display: none;
    }

    &-left-fl {
      display: none;
    }

    &-right-fl {
      display: none;
    }

    &-blur-leaf {
      display: none;
    }

    &-right-leaf {
      display: none;
    }
  }
  .header {
    .owl-carousel .owl-item img {
      display: none;
      width: auto;
      z-index: 0;
    }
  }
  .basket__body {
    width: 510px;
    margin-left: -255px;
  }
}

@media screen and (max-width: 575px) {
  .logo-wrap {
    height: auto;
    width: 100%;
    padding-top: 50px;

  }

  .hat {
    height: auto;
  }

  .main__info {
    flex-wrap: wrap;
    font-size: 11px;
  }

  .header__images-bouquet {
    right: 0;
    top: 158px;
  }

  .about__counter {
    font-size: 100px;
    background-position-y: 33px;
    right: -19px;
    bottom: -83px;
    z-index: 2;
  }

  .about__counter-img {
    right: -47px;
    bottom: 8px;
    height: 200px;
  }

  .second-title {
    font-size: 30px;
  }

  .advantages__list-item {
    padding: 74px 5px 0;
  }

  .advantages__list-text {
    text-align: justify;
  }

  .goods__list {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .goods__list-tab {
    font-size: 12px;
  }

  .goods__item-wrap,
  .basket__wrap{
    grid-template-columns: 1fr;
  }

  .modal-form {
    width: 300px;
    margin-left: -150px;
  }

  .basket__body {
    width: 290px;
    margin-left: -145px;
  }
}

@media screen and (max-width: 359px) {
  .header__images-bouquet {
    right: -369px;
    top: -246px;
  }
}